.loader {
  width: 120px;
  height: 120px;
  box-sizing: border-box;
  border: 5px dotted #4942d1;
  border-radius: 50%;
  margin: 50px auto;
  animation: rotation 2s linear infinite;
  display: block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

